.loading-container{
    background-color: rgb(244, 244, 244);
    height: 100vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}
.loading-img{
    width: 100px;
    height: 100px;
    position: relative;
    border-radius: 50%;
    z-index: 2;
}
#loading-img{
    width: 100%;
    height: 100%;
    display: block;
    z-index: inherit;
}
.loadbar1{
    position: absolute;
    width: 130%;
    left: -15%;
    bottom: -15%;
    height: 130%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-radius: 50%;
    border: 5px solid rgb(11, 172, 78);
    border-bottom-color: transparent;
    border-left-color: transparent;
    border-top-color: transparent;
    -webkit-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
            transform: rotate(-45deg);
    z-index: -1;
    -webkit-animation: loading1 1.35s infinite;
            animation: loading1 1.35s infinite;
}
.loadbar2{
    position: absolute;
    width: 115%;
    left: -7.5%;
    bottom: -7.5%;
    height: 115%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-radius: 50%;
    border: 5px solid rgb(11, 172, 78);
    border-top-color: transparent;
    border-bottom-color: transparent;
    border-right-color: transparent;
    -webkit-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
            transform: rotate(-45deg);
    z-index: -1;
    -webkit-animation: loading2 1.35s infinite;
            animation: loading2 1.35s infinite;
}

@-webkit-keyframes loading1 {
    100%{
        -webkit-transform: rotate(-405deg);
                transform: rotate(-405deg);
    }
}

@keyframes loading1 {
    100%{
        -webkit-transform: rotate(-405deg);
                transform: rotate(-405deg);
    }
}

@-webkit-keyframes loading2 {
    100%{
        -webkit-transform: rotate(315deg);
                transform: rotate(315deg);
    }
}

@keyframes loading2 {
    100%{
        -webkit-transform: rotate(315deg);
                transform: rotate(315deg);
    }
}



